.App {
  scroll-behavior: smooth;
  text-align: center;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}

.thankyou{
  font-size: 20px;
  color: #fad7a0;
  text-align: center;
}

.zattire-button{
  background-color: #E59866;
  border: none;
  color: white;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 7%;
}

.logos{
  width: 400px;  
}

@media only screen and (max-width: 600px) {
 .logos{
   width: 200px;
 }
}

.zattire-button:hover{
  background-color: #935116;
}

.onboarding-logo {
  margin-top: 10vh;
}

.zattire-logo{
  margin-top: 30vh;
  min-width: 50%;
  max-width: 300px;
}

.App-header {
  background-color: #E56B80;
  min-height: 100vh;
  color: white;
}

