.formTemplate {
    margin-top: 5vh;
    text-align: left;
    display: grid;

    @media only screen and (max-width: 600px) {
        grid-template-columns: 20% 80%;
    }
    grid-template-columns: 10% 90%;
    .question {
        font-size: 25px;
        margin: 0;
        color: #fad7a0;
    }

    .define-struct {
        @media only screen and (min-width: 601px) {
            display: grid;
            grid-template-columns: 40% 40%;
        }
    }

    .checkboxed {
        margin-top: 10px;
        margin-bottom: 10px;
    }

    /* The container */
    .container {
        display: block;
        position: relative;
        padding-left: 30px;
        margin-bottom: 12px;
        cursor: pointer;
        font-size: 18px;
        -webkit-user-select: none;
        user-select: none;
    }

    /* Hide the browser's default checkbox */
    .container input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }

    /* Create a custom checkbox */
    .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 20px;
        width: 20px;
        background-color: #eee;
    }

    /* On mouse-over */
    .container:hover input ~ .checkmark {
        background-color: #fad7a0;
    }

    /* When the checkbox is checked,  */
    .container input:checked ~ .checkmark {
        background-color: #fad7a0;
    }

    /* Create the checkmark/indicator (hidden when not checked) */
    .checkmark:after {
        content: "";
        position: absolute;
        display: none;
    }

    /* Show the checkmark when checked */
    .container input:checked ~ .checkmark:after {
        display: block;
    }

    /* Style the checkmark indicator */
    .container .checkmark:after {
        left: 6px;
        top: 2px;
        width: 5px;
        height: 10px;
        border: solid white;
        border-width: 0 3px 3px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    }

    ::placeholder {
        color: rgb(226, 226, 226);
        opacity: 1;
    }

    .count-layout {
        padding: 6px;
        display: grid;
        @media only screen and (max-width: 600px) {
            grid-template-columns: 30% 70%;
        }
        grid-template-columns: 30% 70%;
        margin-right: 5px;
    }

    .next-button {
        background-color: #e59866;
        border: none;
        color: white;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        font-family: 'Montserrat', sans-serif;
        max-width: 100px;
        margin: 4px 0px;
        outline: none;
        cursor: pointer;
    }

    .next-button:hover {
        background-color: #935116;
    }

    .action-buttons {
        display: grid;

        @media only screen and (max-width: 600px) {
            grid-template-columns: 20% 20% 60%;
        }
        grid-template-columns: 10% 10% 80%;

        .enter-container {
            display: inline-flex;
            .enter {
                margin-left: 5px;
                color: #fad7a0;
            }

            .enter-icon {
                margin-left: 2px;
            }
        }
    }

    .action-buttons2 {
        display: grid;

        @media only screen and (max-width: 600px) {
            grid-template-columns: 40% 60%;
        }
        grid-template-columns: 10% 90%;

        .enter-container {
            display: inline-flex;
            .enter {
                margin-left: 5px;
                color: #fad7a0;
            }

            .enter-icon {
                margin-left: 2px;
            }
        }
    }

    .text-input {
        color: white;
        background-color: #e56b80;
        height: 35px;
        font-size: 22px;
        font-family: 'Montserrat', sans-serif;
        outline: none;
        border: none;
        width: 80%;
        @media only screen and (max-width: 600px) {
            margin: 20px 0px 30px 0px;
        }
        margin: 10px 0px 10px 0px;
    }
}
